<template>
  <div class="container" ref="recad-container">
    <!-- indicador -->
    <div class="indicadores">
      <router-link :to="{name: 'recad-form-dadospessoais'}">1</router-link>
      <div class="divisor"></div>
      <router-link :to="{name: 'recad-form-documentacao'}">2</router-link>
      <div class="divisor"></div>
      <router-link :to="{name: 'recad-form-endereco'}">3</router-link>
      <div class="divisor"></div>
      <router-link :to="{name: 'recad-form-contato'}">4</router-link>
      <div class="divisor"></div>
      <router-link :to="{name: 'recad-form-foto'}">5</router-link>
      <div class="divisor"></div>
      <router-link :to="{name: 'recad-form-assinatura'}">6</router-link>
    </div>
    <!-- container do formulario -->
    <transition mode="out-in" name="fade" @after-leave="$jquery($refs['recad-container']).scrollTop()">
      <router-view @finalizado="recadastrar"/>
    </transition>
    <!-- modal de aviso legal -->
    <div ref="aviso-legal" class="modal fade" tabindex="-1" role="dialog" data-backdrop="static">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Atenção</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p class="text-danger text-center mb-2 mr-3">
              <i class="fas fa-exclamation-triangle fa-3x"/>
            </p>
            <p class="text-justify mb-0">Na finalidade de evitar crimes como <span class="text-danger" style="font-weight: 600">Falsidade ideológica</span> e <span class="text-danger" style="font-weight: 600">Falsa identidade</span><span class="text-muted font-italic"> (sob o cartão de regularidade)</span>, algumas informações são apenas para conferência. Se desejar corrigir alguma informação bloqueada, compareça ao CRECI com um documento que comprove a autenticidade da sua informação.</p>
          </div>
          <div class="modal-footer justify-content-center">
            <button type="button" class="btn btn-danger" data-dismiss="modal">Entendi</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import CreciHelper from "../../helpers/CreciHelper";
  import DateHelper from "../../helpers/DateHelper";
  import ImageHelper from "../../helpers/ImageHelper";
  import {WebClientI2br, WebClientCreciPE, WebClientCreciRN} from '../../http'
  export default {
    name: "Home",
    created() {
      //Todos os links que trazem ao recad já ficam ocultos para regionais não-clientes da I2BR, ainda assim, o bloqueio abaixo é para precaução.
      if (!this.podeRecadastrar) {
        this.$toastr.error('Seu regional não tem suporte para o recadastramento');
        this.$router.push({name: 'home'});
        return;
      }

      this.$store.commit('setControls', false);
    },
    mounted() {
      this.$sleep(1000).then(() => this.$jquery(this.$refs['aviso-legal']).modal('show'));
    },
    destroyed() {
      this.$store.commit('setControls', true);
      this.destruirCache();
    },
    methods: {
      async recadastrar(assinatura) {
        if (!this.podeRecadastrar) {
          //Para usuários de regionais não-clientes que de alguma forma conseguiram chegar até aqui
          this.$toastr.error('Seu regional não possui compatibilidade com a ferramenta');
          this.$router.push({name: 'home'});
          return;
        }

        const dados_pessoais = JSON.parse(sessionStorage.getItem('recad_dadospessoais'));
        const documentos = JSON.parse(sessionStorage.getItem('recad_documentacao'));
        const enderecos = JSON.parse(sessionStorage.getItem('recad_endereco'));
        const contato = JSON.parse(sessionStorage.getItem('recad_contato'));
        const foto = JSON.parse(sessionStorage.getItem('recad_foto'));

        const cofeciToken = CreciHelper.getCofeciToken(this.$store.getters.regional);
        const sexo = dados_pessoais.sexo === 1 ? 'M' : (dados_pessoais.sexo === 2 ? 'F' : 'O');
        let tipoSanguineo, fatorRh;
        switch (dados_pessoais.tiposanguineo) {
          case 2:
            tipoSanguineo = 'A';
            fatorRh = 'P';
            break;
          case 3:
            tipoSanguineo = 'A';
            fatorRh = 'N';
            break;
          case 4:
            tipoSanguineo = 'B';
            fatorRh = 'P';
            break;
          case 5:
            tipoSanguineo = 'B';
            fatorRh = 'N';
            break;
          case 6:
            tipoSanguineo = 'AB';
            fatorRh = 'P';
            break;
          case 7:
            tipoSanguineo = 'AB';
            fatorRh = 'N';
            break;
          case 8:
            tipoSanguineo = 'O';
            fatorRh = 'P';
            break;
          case 9:
            tipoSanguineo = 'O';
            fatorRh = 'N';
            break;
          default:
            tipoSanguineo = 'NS';
            fatorRh = 'NS';
        }

        let dados_gravarcorretor = {
          "Token": cofeciToken,
          "CPFCNPJ": documentos.cpf,
          "CodigoInscricao": this.$store.state.creci,
          "IdTipoInscricao": dados_pessoais.tipoinscricao,
          "IdCategoriaInscricao": dados_pessoais.categoriainscricao,
          "IdEstadoCivil": dados_pessoais.estadocivil,
          "IdFormacao": (dados_pessoais.formacao + 1),
          "IdEscolaridade": dados_pessoais.escolaridade,
          "NomePai": dados_pessoais.pai,
          "NomeMae": dados_pessoais.mae,
          "Sexo": sexo,
          "Estrangeiro": "N",
          "Nacionalidade": dados_pessoais.nacionalidade,
          "DataNascimento": DateHelper.date_BRparaSQL(dados_pessoais.datanascimento),
          "Naturalidade": dados_pessoais.naturalidade,
          "UFNaturalidade": dados_pessoais.ufnaturalidade,
          "Reservista": dados_pessoais.reservista ? dados_pessoais.reservista : '0',
          "RegistroIdentidade": documentos.rg,
          "OrgaoEmissor": documentos.rgemissor,
          "UFOrgaoEmissor": documentos.rgufemissor,
          "DataEmissaoRG": DateHelper.date_BRparaSQL(documentos.rgdataemissao),
          "TituloDeEleitor": documentos.tituloeleitoral,
          "UFTituloDeEleitor": "",
          "Zona": documentos.zonaeleitoral,
          "Secao": documentos.secaoeleitoral,
          "TipoSanguineo": tipoSanguineo,
          "FatorRh": fatorRh,
          "Doador": dados_pessoais.doador === 1 ? "S" : "N",
          "Nome": dados_pessoais.nomecompleto,
          "NomeFantasia": dados_pessoais.npa,
          "Situacao": this.$store.state.situacao === 1 ? "A" : "I",
          "DataInscricao": DateHelper.date_BRparaSQL(dados_pessoais.datainscricao),
          "DebitoCreci": this.$store.state.anuidade_absoluta ? "N" : "S",
          "Processo": "N"
        };
        let dados_gravarendereco = {
          "Token": cofeciToken,
          "CodigoInscricao": this.$store.state.creci,
          "IdCategoriaInscricao": dados_pessoais.categoriainscricao,
          "IdTipoEndereco": 1,
          "IdReferencia": 1,
          "Cep": enderecos.endereco_residencial.cep,
          "Logradouro": enderecos.endereco_residencial.logradouro,
          "Numero": enderecos.endereco_residencial.numero,
          "Complemento": enderecos.endereco_residencial.complemento,
          "Bairro": enderecos.endereco_residencial.bairro,
          "Cidade": enderecos.endereco_residencial.cidade,
          "UF": enderecos.endereco_residencial.uf,
          "Correspondencia": enderecos.favorito === 1 ? "S" : "N"
        };
        let dados_gravaremail = {
          "Token": cofeciToken,
          "CodigoInscricao": this.$store.state.creci,
          "IdCategoriaInscricao": dados_pessoais.categoriainscricao,
          "EmailPagina": contato.email1
        };
        let dados_gravarfotoassinatura = {
          "Token": cofeciToken,
          "CodigoInscricao": this.$store.state.creci,
          "IdCategoriaInscricao": dados_pessoais.categoriainscricao,
          "Foto": foto.split('base64,')[1],
          "Assinatura": assinatura.split('base64,')[1]
        };

        //Dados para o histórico no banco interno do App
        let histForm = new FormData();
        histForm.append("context", JSON.stringify(dados_gravarcorretor));

        try {
          //============== GRAVANDO DADOS NO COFECI ==============//
          this.$store.commit('setLoading', 'Registrando no COFECI');
          try {
            //Gravar Corretor
            if (this.$store.state.homologacao) dados_gravarcorretor.homologacao = true;
            await this.$http.cofeciApi.post('/gravarcorretor.php', JSON.stringify(dados_gravarcorretor));

            //Excluir todos os emails
            await this.$http.axios.post(this.$http.cofeciApi.defaults.baseURL + '/excluirtodosemails.php', JSON.stringify({
              "Token": cofeciToken,
              "CodigoInscricao": this.$store.state.creci,
              "IdCategoriaInscricao": dados_pessoais.categoriainscricao,
              "Pessoa": "F"
            }));

            //Excluir todos os telefones
            await this.$http.axios.post(this.$http.cofeciApi.defaults.baseURL + '/excluirtodostelefones.php', JSON.stringify({
              "Token": cofeciToken,
              "CodigoInscricao": this.$store.state.creci,
              "IdCategoriaInscricao": dados_pessoais.categoriainscricao,
              "Pessoa": "F"
            }));


            //Gravar Endereço
            if (this.$store.state.homologacao) dados_gravarendereco.homologacao = true;
            await this.$http.cofeciApi.post('/gravarendereco.php', JSON.stringify(dados_gravarendereco));
            if (enderecos.endereco_comercial) {
              dados_gravarendereco = {
                "Token": cofeciToken,
                "CodigoInscricao": this.$store.state.creci,
                "IdCategoriaInscricao": dados_pessoais.categoriainscricao,
                "IdTipoEndereco": 2,
                "IdReferencia": 4,
                "Cep": enderecos.endereco_comercial.cep,
                "Logradouro": enderecos.endereco_comercial.logradouro,
                "Numero": enderecos.endereco_comercial.numero,
                "Complemento": enderecos.endereco_comercial.complemento,
                "Bairro": enderecos.endereco_comercial.bairro,
                "Cidade": enderecos.endereco_comercial.cidade,
                "UF": enderecos.endereco_comercial.uf,
                "Correspondencia": enderecos.favorito === 2 ? "S" : "N"
              };
              if (this.$store.state.homologacao) dados_gravarendereco.homologacao = true;
              await this.$http.cofeciApi.post('/gravarendereco.php', JSON.stringify(dados_gravarendereco));
            }

            //Gravar Email
            if (this.$store.state.homologacao) dados_gravaremail.homologacao = true;
            await this.$http.cofeciApi.post('/gravaremail.php', JSON.stringify(dados_gravaremail));
            if (contato.email2) {
              dados_gravaremail.EmailPagina = contato.email2;
              await this.$http.cofeciApi.post('/gravaremail.php', JSON.stringify(dados_gravaremail));
            }
            if (contato.email3) {
              dados_gravaremail.EmailPagina = contato.email3;
              await this.$http.cofeciApi.post('/gravaremail.php', JSON.stringify(dados_gravaremail));
            }

            //Gravar Telefone
            const telefonereqs = contato.telefones.map(tel => this.$http.cofeciApi.post('/gravartelefone.php', JSON.stringify({
              "Token": cofeciToken,
              "CodigoInscricao": this.$store.state.creci,
              "IdCategoriaInscricao": dados_pessoais.categoriainscricao,
              "IdTipoTelefone": tel.tipo,
              "DDD": tel.numero.substr(0,2),
              "Numero": tel.numero.substr(2),
              "homologacao": this.$store.state.homologacao ? true : undefined
            })) );
            await this.$http.axios.all(telefonereqs);

            //Gravar Foto e Assinatura
            if (this.$store.state.homologacao) dados_gravarfotoassinatura.homologacao = true;
            await this.$http.cofeciApi.post('/gravarfotoassinatura.php', JSON.stringify(dados_gravarfotoassinatura));

            //Gravar Recadastramento
            await this.$http.cofeciApi.post('/recadastramento.php', JSON.stringify({
              "Token": cofeciToken,
              "CodigoInscricao": this.$store.state.creci,
              "IdCategoriaInscricao": dados_pessoais.categoriainscricao,
              "homologacao": this.$store.state.homologacao ? true : undefined
            }));

            //Validade Documento
            if (CreciHelper.utilizaConselhoNet(this.$store.getters.regional)) {
              try {
                const webClientI2br = new WebClientI2br(documentos.cpf, this.$store.getters.regional, true);
                await webClientI2br.updateDataValidade();
              } catch (e) {
                // eslint-disable-next-line no-console
                console.log(e);
              }
            }
          } catch (e) {
            //Registra no histórico do banco interno do App
            histForm.append("falha", JSON.stringify(e));
            if (e.data.Mensagem) this.$toastr.warning(e.data.Mensagem, 'Aviso do COFECI');
            else this.$toastr.error('Falha ao registrar no COFECI');
            throw new Error('Erro na API COFECI');
          }

          //============== GRAVANDO DADOS NO REGIONAL ============//
          if (!this.$store.state.homologacao) {
            this.$store.commit('setLoading', 'Registrando no CRECI');

            //Para clientes do ConselhoNET
            if (CreciHelper.utilizaConselhoNet(this.$store.getters.regional)) {
              //adaptando o estado civil
              let estadocivil_cnet = null;
              switch (dados_pessoais.estadocivil) {
                case 5:
                  estadocivil_cnet = 4;
                  break;
                case 6:
                  estadocivil_cnet = 5;
                  break;
                case 8:
                  estadocivil_cnet = 6;
                  break;
                default:
                  estadocivil_cnet = dados_pessoais.estadocivil;
              }
              //salvando na base de dados da I2BR
              try {
                //Atualizar Ficha Básica
                let formData = new FormData();
                formData.append("idregional", this.$store.getters.regional);
                formData.append("cpf", documentos.cpf);
                formData.append('nome', dados_pessoais.nomecompleto);
                formData.append('estadocivil', estadocivil_cnet.toString());
                formData.append('formacao', dados_pessoais.formacao===2 ? 5 : (dados_pessoais.formacao===1 ? 2 : dados_pessoais.formacao));
                formData.append('pai', dados_pessoais.pai);
                formData.append('mae', dados_pessoais.mae);
                formData.append('sexo', dados_pessoais.sexo);
                formData.append('nacionalidade', dados_pessoais.nacionalidade);
                formData.append('naturalidade', dados_pessoais.naturalidade);
                formData.append('ufnaturalidade', dados_pessoais.ufnaturalidade);
                formData.append('datanascimento', DateHelper.date_BRparaSQL(dados_pessoais.datanascimento));
                if (dados_pessoais.reservista) formData.append('reservista', dados_pessoais.reservista);
                formData.append('rgnumero', documentos.rg);
                formData.append('rgemissor', documentos.rgemissor+'/'+documentos.rgufemissor);
                formData.append('rgdataemissao', DateHelper.date_BRparaSQL(documentos.rgdataemissao));
                if (documentos.tituloeleitoral) formData.append('tituloeleitor', documentos.tituloeleitoral);
                if (documentos.zonaeleitoral) formData.append('zonaeleitoral', documentos.zonaeleitoral);
                if (documentos.secaoeleitoral) formData.append('secaoeleitoral', documentos.secaoeleitoral);
                formData.append('doador', dados_pessoais.doador);
                formData.append('email1', contato.email1);
                formData.append('email2', contato.email2);
                formData.append('email3', contato.email3);
                await this.$http.conselhoApi.post('/corretor/atualizar/ficha.php', formData);

                //Atualizar Endereços
                formData = new FormData(); //enderecos
                formData.append("idregional", this.$store.getters.regional);
                formData.append("cpf", documentos.cpf);
                formData.append("enderecoresidencial", JSON.stringify({
                  cep: enderecos.endereco_residencial.cep,
                  uf: enderecos.endereco_residencial.uf,
                  cidade: enderecos.endereco_residencial.cidade,
                  bairro: enderecos.endereco_residencial.bairro,
                  logradouro: enderecos.endereco_residencial.logradouro,
                  complemento: enderecos.endereco_residencial.complemento ? enderecos.endereco_residencial.numero+', '+enderecos.endereco_residencial.complemento : enderecos.endereco_residencial.numero
                }));
                if (enderecos.endereco_comercial) formData.append("enderecocomercial", JSON.stringify({
                  cep: enderecos.endereco_comercial.cep,
                  uf: enderecos.endereco_comercial.uf,
                  cidade: enderecos.endereco_comercial.cidade,
                  bairro: enderecos.endereco_comercial.bairro,
                  logradouro: enderecos.endereco_comercial.logradouro,
                  complemento: enderecos.endereco_comercial.complemento ? enderecos.endereco_comercial.numero+', '+enderecos.endereco_comercial.complemento : enderecos.endereco_comercial.numero
                }));
                formData.append("favorito", enderecos.favorito);
                formData.append("quem", "9999");
                await this.$http.conselhoApi.post('/corretor/atualizar/endereco.php', formData);

                //Atualizar Telefones
                formData = new FormData();
                formData.append("idregional", this.$store.getters.regional);
                formData.append("cpf", documentos.cpf);
                formData.append("telefones", JSON.stringify(contato.telefones));
                formData.append("quem", "9999");
                await this.$http.conselhoApi.post('/corretor/atualizar/telefone.php', formData);

                //Atualizar foto
                formData = new FormData();
                formData.append("idregional", this.$store.getters.regional);
                formData.append("cpf", documentos.cpf);
                formData.append("imagem", ImageHelper.dataURItoBlob(foto));
                await this.$http.conselhoApi.post('/corretor/atualizar/foto.php', formData);

                //Atualizar assinatura
                formData = new FormData();
                formData.append("idregional", this.$store.getters.regional);
                formData.append("cpf", documentos.cpf);
                formData.append("imagem", ImageHelper.dataURItoBlob(assinatura));
                await this.$http.conselhoApi.post('/corretor/atualizar/assinatura.php', formData);

                //Registrar Recadastramento
                formData = new FormData();
                formData.append("idregional", this.$store.getters.regional);
                formData.append("cpf", documentos.cpf);
                formData.append("idambiente", "1");
                await this.$http.conselhoApi.post('/corretor/inserir/recadastramento.php', formData);
                this.$toastr.success('Recadastramento registrado');
              } catch (e) {
                // eslint-disable-next-line no-console
                console.log(e);
                //Registra no histórico do banco interno do App
                histForm.append("falha", JSON.stringify(e));
                this.$toastr.error('Falha ao registrar no CRECI');
                throw new Error('Erro na API CRECI');
              }
            }
            //Para o CRECI-PE
            else if (this.$store.getters.regional === 7 || this.$store.getters.regional === 17) {
              const webClientCreciPE = this.$store.getters.regional === 17 ? new WebClientCreciRN(documentos.cpf) : new WebClientCreciPE(documentos.cpf);
              const fichaAtual = await webClientCreciPE.getFichaCadastral();

              fichaAtual.dados_gerais.sexo = dados_pessoais.sexo;
              fichaAtual.info.doador = dados_pessoais.doador === 1;
              fichaAtual.dados_gerais.pai = dados_pessoais.pai;
              fichaAtual.dados_gerais.mae = dados_pessoais.mae;
              //adaptando o estado civil
              switch (dados_pessoais.estadocivil) {
                case 5:
                  fichaAtual.dados_gerais.estado_civil = 4;
                  break;
                case 6:
                  fichaAtual.dados_gerais.estado_civil = 5;
                  break;
                case 8:
                  fichaAtual.dados_gerais.estado_civil = 6;
                  break;
                default:
                  fichaAtual.dados_gerais.estado_civil = dados_pessoais.estadocivil;
              }
              fichaAtual.tituloEleitor.titulo = documentos.tituloeleitoral;
              fichaAtual.tituloEleitor.zona = documentos.zonaeleitoral;
              fichaAtual.tituloEleitor.secao = documentos.secaoeleitoral;
              fichaAtual.contato.endereco_residencial = {
                cep: enderecos.endereco_residencial.cep,
                endereco: enderecos.endereco_residencial.logradouro,
                complemento: enderecos.endereco_residencial.complemento ? enderecos.endereco_residencial.numero+', '+enderecos.endereco_residencial.complemento : enderecos.endereco_residencial.numero,
                bairro: enderecos.endereco_residencial.bairro,
                cidade: enderecos.endereco_residencial.cidade,
                estado: enderecos.endereco_residencial.uf,
              }
              if (enderecos.endereco_comercial) {
                fichaAtual.contato.endereco_comercial = {
                  cep: enderecos.endereco_comercial.cep,
                  endereco: enderecos.endereco_comercial.logradouro,
                  complemento: enderecos.endereco_comercial.complemento ? enderecos.endereco_comercial.numero+', '+enderecos.endereco_comercial.complemento : enderecos.endereco_comercial.numero,
                  bairro: enderecos.endereco_comercial.bairro,
                  cidade: enderecos.endereco_comercial.cidade,
                  estado: enderecos.endereco_comercial.uf,
                }
              }
              fichaAtual.contato.endereco_correspondencia = enderecos.favorito;
              fichaAtual.contato.emails = [];
              if (contato.email1) fichaAtual.contato.emails.push(contato.email1);
              if (contato.email2) fichaAtual.contato.emails.push(contato.email2);
              if (contato.email3) fichaAtual.contato.emails.push(contato.email3);
              fichaAtual.contato.telefones = {
                residencial: contato.telefones.filter(t => t.tipo === 1).map(t => t.numero),
                comercial: contato.telefones.filter(t => t.tipo === 2).map(t => t.numero),
                celular: contato.telefones.filter(t => t.tipo === 3).map(t => t.numero),
              };

              //Converte a assinatura PNG para JPG. Formato aceito pelo CRECI-PE
              const assinaturaJpegBase64 = await ImageHelper.dataURIconvert_async(assinatura, 'image/jpeg');

              try {
                await Promise.all([
                  webClientCreciPE.setFichaCadastral(fichaAtual),
                  webClientCreciPE.setFoto(ImageHelper.dataURItoBlob(foto)),
                  webClientCreciPE.setAssinatura(ImageHelper.dataURItoBlob(assinaturaJpegBase64)),
                ]);
              } catch (e) {
                // eslint-disable-next-line no-console
                console.log(e);
                //Registra no histórico do banco interno do App
                histForm.append("falha", JSON.stringify(e));
                this.$toastr.error('Falha ao registrar no CRECI');
                throw new Error('Erro na API CRECI-PE');
              }
            }
          }

          //Destruindo dados do formulario
          this.destruirCache();

          this.$store.commit('setLoading', 'Atualizando sua conta');
          try {
            await this.$store.dispatch('updateUserData');
          } finally {
            await this.$router.push({name: 'home'}); //saindo
          }
        } finally {
          //Registra no histórico interno do App
          this.$http.axios.post(this.$http.appApi.defaults.baseURL + '/recad/registrar.php', histForm, {
            headers: {'Authorization': this.$store.state.token}
          });
          this.$store.commit('setLoading', false);
        }
        //====================
      },
      destruirCache() {
        sessionStorage.removeItem('recad_dadospessoais');
        sessionStorage.removeItem('recad_documentacao');
        sessionStorage.removeItem('recad_endereco');
        sessionStorage.removeItem('recad_contato');
        sessionStorage.removeItem('recad_foto');
      }
    },
    computed: {
      podeRecadastrar() {
        return CreciHelper.utilizaConselhoNet(this.$store.getters.regional) || this.$store.getters.regional === 7;
      }
    }
  }
</script>

